<template>
  <div class="box">
    <div style="display: flex;justify-content: space-between;height: 40px;">
      <div></div>
    </div>
    <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="notApproved.tableData"
      tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
      :header-cell-style="headerStyle">
      <el-table-column type="index" label="" width="50">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span></span>
        </template>
      </el-table-column>
      <el-table-column v-for="(item, index) in notApproved.tableMsg" :key="index" :prop="item.key" :label="item.name">
      </el-table-column>
      <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <el-button @click="StateManagementChange(scope)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--page start   current-page:handleSizeChangepage-sizetotalhandleCurrentChange-->
    <el-pagination style="margin-top: 10px;" background @size-change="notApprovedSizeChange"
      @current-change="notApprovedCurrentChange" :current-page="notApproved.currentPage"
      :page-sizes="[10, 20, 50, 100,200]" :page-size="notApproved.perPageSize"
      layout="total, sizes, prev, pager, next, jumper" :total="notApproved.total">
    </el-pagination>

    <!--  -->
    <el-dialog title="-" :visible.sync="StateManagement.dialogVisible" width="60%"
      :before-close="handleClose">
      <span>
        <div>
          <div style="width: 100%;text-align: left;margin-bottom: 10px;">{{StateManagement.msg.mbmi_mer_name}}
          </div>
          <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable"
            :data="StateManagement.tableData" tooltip-effect="dark" :row-style="{'height':'10px'}"
            :cell-style="{'padding':'5px 0px'}" :header-cell-style="headerStyle">
            <el-table-column type="index" label="" width="120">
            </el-table-column>
            <el-table-column prop="bsi_name" label="">
            </el-table-column>
            <el-table-column label="" width="450">
              <template slot-scope="scope">
                <div style="display: flex;">
                  <div style="width: 40%;">
                    <el-radio v-model="scope.row.bma_from_status" label="2"></el-radio>
                    <el-radio v-model="scope.row.bma_from_status" label="3"></el-radio>
                  </div>
                  <el-input style="width: 60%;" v-show="scope.row.bma_from_status==3" size="mini"
                    v-model="scope.row.bma_from_describer" placeholder="">
                  </el-input>
                </div>
              </template>
            </el-table-column>
          </el-table>
          <!--page start   current-page:handleSizeChangepage-sizetotalhandleCurrentChange-->
          <el-pagination style="margin-top: 10px;" background @size-change="StateManagementSizeChange"
            @current-change="StateManagementCurrentChange" :current-page="StateManagement.currentPage"
            :page-sizes="[10, 20, 50, 100,200]" :page-size="StateManagement.perPageSize"
            layout="total, sizes, prev, pager, next, jumper" :total="StateManagement.total">
          </el-pagination>
        </div>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="StateManagement.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm()"> </el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        notApproved: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          tableMsg: [
            // { key: 'type', name: '' },
            { key: 'mbmi_mer_name', name: '' },
            { key: 'bii_name', name: '' },
            // { key: 'mbmi_contacts', name: '' },
            { key: 'mbmi_phone', name: '' }
          ],
          tableData: []
        },
        // 
        tableMsg: [{ key: 'number', name: '' },
        // { key: 'type', name: '' },
        { key: 'name', name: '' },
        { key: 'industry', name: '' },
        { key: 'contacts', name: '' },
        { key: 'phone', name: '' }
        ],
        StateManagement: {
          //
          radio: '1',
          input: '',
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          input: '',
          //
          dialogVisible: false,
          msg: {},
          tableData: []
        },
        //
        headerStyle: { 'background': '#4B86BC', 'color': '#ffffff', 'borderRight': '1px solid #E3E5EB', 'padding': '0px', 'height': '45px', 'line-height': '45px' },
        //
        tableData: [{
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    },
    created() {
      this.getData()
    },
    methods: {
      /**
       * 
      */
      getData() {
        let that = this;
        let _jsondata_ = {
          currentPage: this.notApproved.currentPage,
          perPageSize: this.notApproved.perPageSize,
          manager_status: 1,
          obj_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: this.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: this.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: this.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.merchant_pending_review_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log(":" + JSON.stringify(res));
          if (res.data.status == 'success') {
            loading.close();
            that.notApproved.tableData = res.data.rows;
            that.notApproved.total = res.data.total
          } else {
            that.notApproved.tableData = [];
            console.info("");
            loading.close();
            if (res.data.msg === '') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log(":" + JSON.stringify(error));
          loading.close();
          that.notApproved.tableData = [];
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(error.message), '!', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log(":" + JSON.stringify(res));
          loading.close();
          that.notApproved.tableData = [];
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(res.message), '', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        });
      },

      /**
       * 
       */
      StateManagementChange(value) {
        this.StateManagement.dialogVisible = true
        this.StateManagement.msg = value.row
        let that = this;
        let _jsondata_ = {
          currentPage: this.StateManagement.currentPage,
          perPageSize: this.StateManagement.perPageSize,
          bma_mer_uuid: value.row.mbmi_uuid
        };
        const loading = this.$loading({
          lock: true,
          text: '...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.other_affiliated_outlets_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log(":" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.StateManagement.tableData = res.data.rows;
            that.StateManagement.total = res.data.total
            loading.close();
          } else {
            that.StateManagement.tableData = [];
            console.info("");
            loading.close();
            if (res.data.msg === '') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log(":" + JSON.stringify(error));
          loading.close();
          that.StateManagement.tableData = [];
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(error.message), '!', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log(":" + JSON.stringify(res));
          loading.close();
          that.StateManagement.tableData = [];
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(res.message), '', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        });
      },

      /**
       * 
      */
      /**
        * @param //bma_uuid           
        * @param //bma_from_status      12
        * @param //bma_from_describer 
        * @param //bma_to_status        12
        * @param //bma_to_describer   
      */
      submitForm() {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          console.log(this.StateManagement.tableData, "---------------------------");

          let everyMsg = this.StateManagement.tableData.filter(res => {
            return res.bma_from_status == '3'
          })

          let everyShow = everyMsg.every(res => {
            return res.bma_from_describer 
          })
          if (!everyShow) {
            this.$message({
              type: 'warning',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;"></span>'
            });
            return
          }
          let that = this;
          let msg = this.StateManagement.tableData.map(res => {
            return {
              bma_uuid: res.bma_uuid,
              bma_from_status: res.bma_from_status,
              bma_from_describer: res.bma_from_describer,
              bma_to_status: "",
              bma_to_describer: "",
              bma_to_bank_center_id: res.bma_to_bank_center_id,
              bma_to_bank_province_id: res.bma_to_bank_province_id,
              bma_to_bank_city_id: res.bma_to_bank_city_id,
              bma_to_bank_county_id: res.bma_to_bank_county_id,
              bma_to_bank_station_uuid: res.bma_to_bank_station_uuid,
            }
          })
          let _jsondata_ = {
            dataList: msg
          };
          const loading = this.$loading({
            lock: true,
            text: '...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0)'
          });
          var a1 = this.customGlobal.postHttpClient(
            this.customGlobal.serverUrl.other_affiliate_outlets_modify_status_update,
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            { "Content-Type": "application/x-www-form-urlencoded" },
            this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
          );
          a1.then((res) => {
            console.log(":" + JSON.stringify(res));
            if (res.data.status == 'success') {
              loading.close();
              this.StateManagement.dialogVisible = false
              this.getData();
            } else {
              console.info("");
              loading.close();
              if (res.data.msg === '') {
                this.$router.push({ path: '/login' });
              }
            }
          }, error => {
            console.log(":" + JSON.stringify(error));
            loading.close();
            // this.companyDialogFormVisible =false;//dialog
            this.$alert(JSON.stringify(error.message), '!', {
              confirmButtonText: '',
              callback: action => {

              }
            });
          }).catch((res) => {
            console.log(":" + JSON.stringify(res));
            loading.close();
            // this.companyDialogFormVisible =false;//dialog
            this.$alert(JSON.stringify(res.message), '', {
              confirmButtonText: '',
              callback: action => {

              }
            });
          });
        }).catch(() => {
          return
        });
      },


      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => { });
      },
      /**-------------start----*/
      notApprovedSizeChange(val) {
        // console.log(` ${val} `);
        this.notApproved.perPageSize = val;
        this.getData();
      },
      notApprovedCurrentChange(val) {
        // console.log(`: ${val}`);
        this.notApproved.currentPage = val;
        this.getData();
      },
      /**-------------start----*/
      StateManagementSizeChange(val) {
        // console.log(` ${val} `);
        this.StateManagement.perPageSize = val;
        this.StateManagementChange();
      },
      StateManagementCurrentChange(val) {
        // console.log(`: ${val}`);
        this.StateManagement.currentPage = val;
        this.StateManagementChange();
      },
    },
  }
</script>
<style scoped>
  .box>>>.el-table__fixed-right::before,
  .el-table__fixed::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #EBEEF5;
    z-index: 4;
  }
</style>


