<script>
  import { regionData, CodeToText } from "element-china-area-data";
  export default {
    data() {
      return {
        indexOBJ: 0,
        pca: {
          province: '',//
          city: '',//
          area: '',//
        },
        regionData: regionData,
        industrySelectionList: [],
        //
        adoptApproved: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          tableMsg: [
            { key: 'mbmi_mer_name', name: '' },
            { key: 'bii_name', name: '' }
          ],
          tableData: []
        },
        //
        ruleForm: {
          loc: "",//
          bmi_mer_name: '',//
          bmi_mer_alias: '',//
          bms_license_code: '',//
          bmi_industry_id: '',//
          bms_work_t1: '',//
          bms_work_t2: '',//
          bms_address: '',//
          mbmi_mid: "",//
          bmi_login_id: "",//
          bmi_password: '123456',//
          selectedOptions: [],//
        },
        //
        rules: {
          bmi_mer_name: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '', trigger: 'blur' },
            { required: true, message: '', trigger: 'blur' },
          ],
          bmi_login_id: [
            { pattern:/^[0-9]{11}$/, message: '', trigger: 'blur' },
            { required: true, message: '', trigger: 'blur' },
            { min: 11, max: 11, message: ' 11 ', trigger: 'blur' }
          ],
          bmi_password: [
            { required: true, message: '', trigger: 'blur' },
            { min: 6, max: 18, message: ' 618 ', trigger: 'blur' }
          ],
          bmi_mer_alias: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '', trigger: 'blur' },
            { required: true, message: '', trigger: 'blur' },
            // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          bms_license_code: [
            { pattern:/^[A-Za-z0-9]{18}$/, message: '18', trigger: 'blur' },
            { required: true, message: '', trigger: 'blur' },
            // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          bms_address: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
          ],
          mbmi_mid: [
            { pattern:/^[A-Za-z0-9]{15}$/, message: '15', trigger: 'blur' },
            { required: true, message: '', trigger: 'blur' },
            // { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
          ],
          bms_work_t1: [
            { required: true, message: '', trigger: 'change' }
          ],
          bms_work_t2: [
            { required: true, message: '', trigger: 'change' }
          ],
          bmi_industry_id: [
            { required: true, message: '', trigger: 'change' }
          ],
          selectedOptions: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
        },
        // 
        tableMsg: [
          { key: 'date', name: '' },
          { key: 'date', name: '' },
          { key: 'date', name: '' },
          { key: 'date', name: '' },
          { key: 'date', name: '' },
          { key: 'date', name: '' }
        ],
        // 
        functionMsg: {
          name: "xxxxx",
          dialogVisible: false,
          msg: [
            { radio: '1', name: '' },
            // { radio: '1', name: '' },
            { radio: '1', name: '' },
            // { radio: '1', name: '' }
          ]
        },
        // 
        serviceChargeMsg: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          obj: [],
          msg: [
            { name: '', key: 'di_describe' },
            { name: '', key: 'doi_active_id' },
          ]
        },
        // 
        merchantInformation: {
          dataMsg: {},
          obj: [],
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          msg: [
            { value: '1', label: '' },
            { value: '2', label: '' },
            { value: '3', label: '' },
            { value: '4', label: '' },
            { value: '5', label: '' }
          ],
        },
        // 
        franchiseOutlets: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          name: "xxxxxx",
          dialogVisible: false,
          obj: {},
          setData: [],
          msg: [
            { name: '', key: 'center_name' },
            { name: '', key: 'province_name' },
            { name: '', key: 'city_name' },
            { name: '', key: 'county_name' },
            { name: '', key: 'station_name' },
          ],
        },
        // 
        newlyAdded: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          msg: [
            { radio: '2', name: '' },
            { radio: '2', name: '' },
          ],
          options: [
            { value: '1', label: '' },
            { value: '2', label: '' },
            { value: '3', label: '' },
            { value: '4', label: '' },
            { value: '5', label: '' }
          ],
        },
        //
        headerStyle: { 'background': '#4B86BC', 'color': '#ffffff', 'borderRight': '1px solid #E3E5EB', 'padding': '0px', 'height': '45px', 'line-height': '45px' },
        //
        addQueryList: [],
        currentCompanyId: '',
        mid: '',
        tableData: [{
          index: 1,
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    },
    created() {
      console.log(this.customGlobal.userInfoExt, "***********************************");
      this.Select_industry()
      // this.addQueryClick()
      this.getData()
      this.bank_commodity()
    },
    watch: {
      'newlyAdded.dialogVisible'(newView) {
        if (newView) {
          this.addQueryClick('', this.addQueryList)
        }
      }
    },
    methods: {
      /**
       * 
      */
      getData() {
        let that = this;
        let _jsondata_ = {
          currentPage: this.adoptApproved.currentPage,
          perPageSize: this.adoptApproved.perPageSize,
          manager_status: 1,
          obj_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: this.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: this.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: this.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.merchant_examination_passed_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log(":" + JSON.stringify(res));
          if (res.data.status == 'success') {
            loading.close();
            that.adoptApproved.tableData = res.data.rows;
            if (that.adoptApproved.tableData.length == 0) {
              return
            }
            console.log(res.data.rows[0].bank_mer_permissions, "-***********************************");
            res.data.rows.forEach((element, i) => {
              that.adoptApproved.tableData[i].bank_mer_permissions = element.bank_mer_permissions.map((res1 => {
                return res1.bma_permissions_type
              }))
            })
            console.log(that.adoptApproved.tableData, "--------------**********************");

            that.adoptApproved.total = res.data.total
          } else {
