      /**
       * 
      */
      submitData(formName) {
        let show = this.addQueryList.every(res => {
          console.log(res.bank_fee * 1 , res.mbmp_fee * 1 , res.mbpb_fee * 1);
          let bank_fee=res.bank_fee?res.bank_fee:0
          let mbmp_fee=res.mbmp_fee?res.mbmp_fee:0
          let mbpb_fee=res.mbpb_fee?res.mbpb_fee:0
          let number = bank_fee * 1 + mbmp_fee * 1 + mbpb_fee * 1
          return number < 30 || number == 30
        })

        if (!show) {
          this.$alert('++30%', '', {
            confirmButtonText: '',
            callback: action => { }
          });
          return
        }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.submitForm()
          } else {
            console.log('error submit!!');
            this.$message({
              message: '',
              type: 'warning'
            });
            return false;
          }
        });
